}
.avantage-background-icone {
  display: flex;
  border-top: solid 1px #149ac0;
  border-bottom: solid 1px #215ca8;
  width: 60px;
  height: 60px;
  border-top-right-radius: 50%;
  border-bottom-left-radius: 50%;
  margin: 0 auto;
  align-items: center;
  transition: rotate 1s;
}
.avantage-background-icone:hover {
  rotate: 180deg;
}
.avantage-background-icone:hover img,
.avantage-background-icone :focus {
  rotate: -180deg;
}
.avantage img {
  transition: rotate 1s;
  margin: auto;
  margin-top: 10%;
  margin-bottom: 10%;
  transform: scale(0.6);
}
.avantage-title {
  width: 80%;
  text-align: center;
  margin: 0 auto;
  font-size: 14px;
}
.avantage-description {
  height: 1px;
  width: 80%;
  margin: 0 auto;
  opacity: 0;
  transition: all 1s ease-out;
}
.avantage-description-hide {
  margin: 0 auto;
  width: 80%;
  height: 80px;
  opacity: 1;
  transition: all 1s ease-in;
}
.avantage-text {
  text-align: justify;
  height: 0;
  opacity: 0;
  font-size: 14px;
  transition: all 3s ease-in;
  transition-delay: 6s opacity;
}
.avantage-text-hide {
  padding: 15px;
  background-color: lightgray;
  text-align: justify;
  color: #242e60;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  height: 100%;
  font-size: 12px;
  transition-delay: opacity 3s;
  transition: opacity 4s ease-in;
  transition-delay: opacity 6s;
}

