.mainHome__text {
  width: 100%;
  height: auto;
  &--corps {
    display: flex;
    flex-flow: column;
    margin: auto;
    width: 80%;
    & h2 {
      font-family: "Montserrat";
      font-weight: 500;
      font-size: 32px;
      margin-top: 100px;
      @include responsive(tablet) {
        font-size: 16px;
        margin-top: 15px;
      }
    }
  }
}
h1 {
  font-family: "Montserrat";
  font-size: 40px;
  @include responsive(tablet) {
    font-size: 20px;
  }
}

.container__description,
.container__product {
  padding-top: 40px;
  padding-left: 50px;
  padding-right: 50px;
  @include responsive(tablet) {
    padding: 15px;
  }
  & p {
    font-family: "Montserrat";
    font-size: 20px;
    text-align: justify;
    @include responsive(tablet) {
      font-size: 12px;
    }
  }
}

.container__product--gallery {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  min-height: auto;
  margin-left: 50px;
  margin-right: 50px;
  margin-bottom: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

.gallery__card {
  width: 200px;
  height: 225px;
  background-color: $secondColor;
  display: flex;
  flex-flow: column;
  padding-top: 20px;
  margin-bottom: 40px;
  @include buttonradius(15px);
  @include responsive(tablet) {
    width: 120px;
    height: 150px;
    padding-top: 5px;
  }

  &--img {
    min-height: 150px;
    width: 95%;
    margin: 0 auto;
    border-radius: 10px;
    text-align: center;
    color: white;
    background-size: contain;
    @include responsive(tablet) {
      min-height: 100px;
    }
  }
  & h3 {
    margin-top: 5px;
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: white;
    padding-left: 10px;
    @include responsive(tablet) {
      font-size: 10px;
    }
  }
}
.article__wellfare-resume {
  position: relative;
  background: transparent;
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 50px;
  margin-bottom: 100px;
  @include responsive(tablet) {
    margin-top: 10px;
    margin-bottom: 20px;
  }

  & p {
    font-family: "Montserrat";
    font-size: 20px;
    text-align: justify;
    @include responsive(tablet) {
      font-size: 12px;
    }
  }
}
